:root {
  --bg-main: #1d1d1d;
  --bg-accent: #2b2b2b;
  --bg-accent1: #3d3d3d;
  --fg-main: #d8d8d8;
  --fg-muted: #acacac;
  --fg-dead: #7e7e7e;
  --primary: rgb(13, 114, 114);
  --primary-muted: rgb(27, 70, 70);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #1d1d1d;
  background-color: var(--bg-main);
  font-family: monospace;
  color: #d8d8d8;
  color: var(--fg-main);
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: 100%;
  padding: 1em;
  grid-gap: 1em;
  gap: 1em;
  min-height: calc(100vh - 80px - 60px);
}

.pane {
  flex: 1 1;
}

input, select, textarea {
  background-color: #3d3d3d;
  background-color: var(--bg-accent1);
  border: 1px solid #7e7e7e;
  border: 1px solid var(--fg-dead);
  color: #d8d8d8;
  color: var(--fg-main);
}

.text-input {
  resize: vertical;
  width: 100%;
  border-radius: 4px;
  padding: 0.5em;
  font-size: 1em;
  min-height: 2em;
}

.page-header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80px;
  background-color: rgb(13, 114, 114);
  background-color: var(--primary);
  color: #d8d8d8;
  color: var(--fg-main);
  font-weight: bold;
  box-shadow: 1px 1px 15px 1px rgba(0,0,0,0.10);
}

.page-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  background-color: rgb(27, 70, 70);
  background-color: var(--primary-muted);
  color: #d8d8d8;
  color: var(--fg-main);
}

.sub-option {
  padding-left: 2.5em;
  padding-bottom: 0.1em;
}

@media only screen and (min-width: 1500px) {
  .container {
    width: 80%;
  }
}

@media only screen and (min-width: 992px) {
  .container {
    flex-direction: row;
    margin-top: 80px;
  }

  .page-header {
    position: fixed;
    top: 0;
  }
}
.card {
  background-color: var(--bg-accent);
  box-shadow: 1px 1px 15px 1px rgba(0,0,0,0.10);
  border-radius: 4px;
  padding: 0.5em;
}
button {
  background-color: var(--bg-accent1);
  border: 1px solid var(--fg-dead);
  color: var(--fg-main);
  border-radius: 2px;
  padding: 4px 8px;
  cursor: pointer;
  transition: background-color 250ms ease;
}

button:hover {
  background-color: var(--bg-accent);
}

button:active {
  background-color: var(--fg-dead);
}

.tooltip-text {
  position: absolute;
  pointer-events: none;
  text-wrap: nowrap;
  top: 1.5rem;
  left: 10px;
  background-color: var(--bg-main);
  color: var(--fg-muted);
  padding: 4px 8px;
  border-radius: 4px;
  opacity: 0.8;
}
.checkbox-container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  align-items: center;
  grid-gap: 0.5em;
  gap: 0.5em;
  cursor: pointer;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}

.checkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  height: 20px;
  width: 20px;
  background-color: var(--bg-accent1);
}

.checkbox-container input ~ .checkmark {
  border-radius: 4px;
  transition: background-color 250ms ease;
}

.checkbox-container:hover input ~ .checkmark {
  background-color: var(--primary-muted);
}

.checkbox-container input:checked ~ .checkmark {
  background-color: var(--primary);
}

.checkbox-container input:checked:hover:enabled ~ .checkmark {
  background-color: var(--primary-muted);
}

.checkbox-container input:disabled ~ .checkmark {
  background-color: var(--fg-dead);
  cursor: not-allowed;
}

.checkmark:after {
  content: "";
  position: relative;
  left: 6px;
  top: 2px;
  display: none;
}

.checkbox-container input:checked ~ .checkmark:after {
  display: block;
}

.checkbox-container .checkmark:after {
  width: 5px;
  height: 10px;
  border: solid var(--fg-main);
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.checkbox-container input:focus ~ .checkmark{
  outline: rgb(0, 110, 255) 2px solid;
}
.collapsible-card {
  background-color: var(--bg-accent);
  border-radius: 4px;
  padding: 1em;
  margin: 1em 0;
  box-shadow: 1px 1px 15px 1px rgba(0,0,0,0.10);
  height: auto;
  overflow: hidden;
}

.collapsible-card .titlebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin-bottom: 15px;
}

.collapsible-card .titlebar .title-holder {
  flex: 1 1;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
}

.collapsible-card .titlebar .actions .collapse-btn {
  width: 25px;
  height: 25px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 1.2em;
  color: var(--fg-main);
}

.collapsible-card .titlebar .actions .collapse-btn .collapse-icon{
  transition: all 200ms;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

.collapsible-card .titlebar .actions .collapse-btn .collapsed-icon{
  transition: all 200ms;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}


.number-container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  align-items: center;
  grid-gap: 0.5em;
  gap: 0.5em;
  cursor: pointer;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}

.number-container input {
  cursor: pointer;
  height: 2em;
  width: 4em;
  padding-left: 0.2rem;
  padding-right: 0.2rem;
  border-radius: 4px;
  border: 1px solid var(--fg-main);
}
.rule-container {
  border: 1px solid var(--fg-dead);
  border-radius: 4px;
  padding: 0.5em;
  display: flex;
  flex-direction: column;
  grid-gap: 0.5em;
  gap: 0.5em;
  width: 100%;
}

.rule-container .row {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
  
}

.rule-container .row-right {
  justify-content: flex-end;
}

.rule-container .reg-pre,
.rule-container .reg-su {
  color: gray;
  font-size: 1rem;
}

.rule-container .reg-pre {
  margin-right: -0.5rem;
}

.rule-container .reg-su {
  margin-left: -0.5rem;
}

.rule-container input {
  flex: 1 1;
  height: 2em;
  border-radius: 4px;
  padding-left: 0.2rem;
  padding-right: 0.2rem;
}

.rule-container .row select {
  height: 2em;
  border-radius: 4px;
}



.rules-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
}

.rules-container .add-rule-btn {
  margin: 0 1em;
  width: 100px;
}

.rule-container .info-text {
  margin-left: 1em;
  color: var(--fg-main);
}
